.toxicity-classifier-container {
    display: flex;
    flex-direction: column;
    padding: 64px;
    height: 100vh;
    align-items: center;
    justify-content: center;
    margin: 64px;
    background: rgb(22, 27, 34);
    border-radius: 12px;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
}

@media only screen and (max-width: 1024px) {
    .toxicity-classifier-container {
        margin: 12px;
        padding: 64px 12px 12px 12px;
        height: 100vh;
        justify-content: flex-start;
        height: 130vh;
        margin-bottom: 64px;
    }

}

@media only screen and (max-width: 768px) {
    .toxicity-classifier-container {
        margin: 12px;
        padding: 12px;
        height: 100vh;
        align-items: center;
        justify-content: center;
        margin-bottom: 64px;
    }
}


.toxicity-classifier-container>div {
    /* padding: 10px; */
}

.input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.ui.big.input {
    width: 30vw;
    min-width: 300px;
    margin-bottom: 32px;
}

.header {
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;
    justify-content: center;
    align-items: center;
}

.header>span {
    margin-top: 24px;
    color: #f0f0f0;
    font-weight: 300;
    font-size: 2rem;
    text-align: center;
}

.results {
    height: 30vh;
    width: 45vw;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.ui.icon.positive.button {
    width: 70px;
    text-align: center;
    justify-content: center;
}

.prediction-box {
    display: flex;
    flex-direction: column;
    width: 200px;
    height: 100px;
    background-color: rgb(13, 17, 23);
    margin-left: 12px;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 12px;
    padding: 12px;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
}

.prediction-box:hover {
    filter: brightness(1.1);
    -webkit-filter: brightness(1.1);
}

.prediction-box>span:first-child {
    font-size: 1.5rem;
    color: #777;
}

.prediction-box>span:last-child {
    font-size: 1rem;
    color: #777;
    margin-top: 6px;
    letter-spacing: 0.05rem;
}

@media only screen and (max-width: 768px) {
    .prediction-box {
        width: 100px;
        height: 50px;
        padding: 6px;
    }

    .prediction-box>span:first-child {
        font-size: 1.1rem;
        color: #777;
    }

    .prediction-box>span:last-child {
        font-size: 0.6rem;
        color: #777;
        margin-top: 3px;
        letter-spacing: 0.05rem;
    }

}

.predictions {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-top: 32px;
}

.ui.active.loader {
    margin-top: 64px;
}